import Vuex from 'vuex';
import Vue from 'vue';

export default function(options = {}) {
  Vue.use(Vuex);
  return new Vuex.Store({
    state: Object.assign({
      pageTitle: '',
      loginState: true,
      appReady: true,
      ajaxState: {
        show: true,
        flag: 0,
        tip: '',
      },
      pageTranSet: {
        popState: false,
        transitionType: 'fade',
        transitionMode: 'out-in',
      },
    }, options.state),
    mutations: Object.assign({
      ajaxState(state, payload) {
        Object.assign(state.ajaxState, payload);
      },
      pageTranSet(state, payload) {
        Object.assign(state.pageTranSet, payload);
      },
      loginState(state, {loginState}) {
        state.loginState = loginState;
      },
      appReady(state, {appReady}) {
        state.appReady = appReady;
      },
      pageTitle(state, {pageTitle}) {
        state.pageTitle = pageTitle;
      },
    }, options.mutations),
    getters: Object.assign({}, options.getters),
    actions: Object.assign({}, options.actions),
    plugins: [].concat(options.plugins||[]),
    strict: false,
  });
}



