/**
 * @功能名称: 引入网络库 flyio
 * @文件名称: fly.js
 * @Date: 2018/5/21 下午5:17.
 * @Author: liux
 */
var fly = window.fly=require("flyio");
export default function(store) {

//添加请求拦截器
  fly.config.timeout=10000;
  fly.interceptors.request.use((request)=>{
    if (store.state.ajaxState.show) {
      store.commit('ajaxState', {flag: store.state.ajaxState+1});
    } else {
      store.commit('ajaxState', {show: true});
    }
    return request;
  })

//添加响应拦截器，响应拦截器会在then/catch处理之前执行
  fly.interceptors.response.use(
      (response) => {
        store.commit('ajaxState', {flag: store.state.ajaxState-1});
        //只将请求结果的data字段返回
        return response.data
      },
      (err) => {
        //发生网络错误后会走到这里
        store.commit('ajaxState', {flag: store.state.ajaxState-1});
        alert(JSON.stringify(err));
      }
  )

  return fly;
}



