<style>
  @import "./pageTransAnimate.less";

  .sub-view {
    position: absolute;
    min-height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    padding-top: 2.2rem;
  }

  .viewch-transition {
    transition: all .3s ease;
  }

  .viewch-enter, .viewch-leave {
    opacity: 0;
    transform: translate3d(0, 0, 10px);
  }

  .viewch2-transition, .viewch3-transition, .viewch4-transition {
    transition: all .4s ease;
  }

  .viewch2-enter, .viewch2-leave {
    opacity: 0;
    transform: translate3d(0, 30rem, 1rem);
  }

  .viewch3-enter, .viewch3-leave {
    opacity: 0;
    transform: translate3d(30rem, 0, 1rem);
  }

  .viewch4-enter, .viewch4-leave {
    opacity: 0;
    transform: scaleY(0.01);
  }

  [v-cloak] {
    display: none;
  }

  input::-webkit-input-placeholder {
    color: darkgray;
  }

  .fade-enter-active, .fade-leave-active {
    -webkit-transition: all .5s;
    transition: all .5s;
  }

  .fade-enter, .fade-leave-active {
    opacity: 0.01;
  }

  .viewch4-enter-active, .viewch4-leave-active {
    -webkit-transition-duration: 0.4s;
    transition-duration: 0.4s;
    opacity: 1;
    -webkit-transform: scaleY(1);
    transform: scaleY(1);
  }

  .viewch4-enter, .viewch4-leave-active {
    opacity: 0.01;
    -webkit-transform: scaleY(0.01);
    transform: scaleY(0.01);
  }

  .slider-enter, .slidel-enter {
    opacity: 0.01;
  }

  .slider-enter-active, .slider-leave-active, .slidel-enter-active, .slidel-leave-active {
    min-height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }

  .slider-enter-active {
    -webkit-animation: slider-in .5s;
    animation: slider-in .5s;

  }

  .slider-leave-active {
    -webkit-animation: slider-out .5s;
    animation: slider-out .5s;
  }

  .slidel-enter-active {
    -webkit-animation: slidel-in .5s;
    animation: slidel-in .5s;
  }

  .slidel-leave-active {
    -webkit-animation: slidel-out .5s;
    animation: slidel-out .5s;
  }

  .ajax-ing {
    position: fixed;
    z-index: 101;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.2);
  }

  .ajax-ing .loader {
    position: absolute;
    width: 1.4rem;
    height: 1.4rem;
    top: 50%;
    left: 50%;
    margin-left: -0.7rem;
    margin-top: -0.7rem;
    border-top: 0.1rem solid rgba(255, 255, 255, 0.2);
    border-right: 0.1rem solid rgba(255, 255, 255, 0.2);
    border-bottom: 0.1rem solid rgba(255, 255, 255, 0.2);
    border-left: 0.1rem solid #ffffff;
    border-radius: 50%;
    animation: loader 1.1s infinite linear;
  }

  .ajax-ing .text {
    position: absolute;
    width: 1.4rem;
    height: 1.4rem;
    top: 50%;
    left: 50%;
    margin-left: -0.7rem;
    margin-top: -0.7rem;
    line-height: 1.4rem;
    font-size: 0.75rem;
    color: #00c319;
    z-index: 1001;
  }

  @keyframes loader {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }

  @-webkit-keyframes loader {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
.page-box{
  height: 100%;
}
</style>

<template>
  <div class="page-box">
    <transition :name="pageTranSet.transitionType" :mode="pageTranSet.transitionMode" appear>
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </transition>

    <div v-show="ajaxState.flag>0" class="ajax-ing">
      <div class="loader"></div>
      <div class="text f-tac">{{ajaxState.tip}}</div>
    </div>
    <toast ref="toast"></toast>
    <alert ref="alert"></alert>
    <confirm ref="confirm"></confirm>
  </div>
</template>
<script>
  import alert from 'component/alert';
  import toast from 'component/toast';
  import confirm from 'component/confirm';

  import {mapState} from 'vuex';

  export default {
    data() {
      return {
      };
    },
    computed: {
      ...mapState([
        'pageTitle',
        'loginState',
        'ajaxState',
        'pageTranSet',
      ])
    },
    watch: {
      pageTitle(title) {
        document.title = title;
      },
      loginState(state) {

      },
    },
    created() {
      window.web_app = this;
    },
    components: {
      alert,
      confirm,
      toast,
    },
  };

</script>



