/**
 * Created by liux on 17/9/19.
 * vue 原型扩展，快捷方法
 * 包装vue resource 服务正常返回时触发业务 then，
 * */
import jsonlint from 'assets/jsonlint'
export default function install (Vue, options){
  Vue.prototype.alert = function () {
    return this.$root.$children[0].$refs.alert.show(...arguments)
  };
  Vue.prototype.confirm = function () {
    return this.$root.$children[0].$refs.confirm.show(...arguments)
  };
  Vue.prototype.toast =function () {
    return this.$root.$children[0].$refs.toast.show(...arguments)
  };
  Vue.prototype.report =function () {
    return this.$root.report(...arguments)
  };
  Vue.prototype.commit =function () {
    return this.$store.commit(...arguments)
  };
  Vue.prototype.dispatch =function () {
    return this.$store.dispatch(...arguments)
  };

  Vue.prototype.post =function () {
    return options.http.post(...arguments)
  };
  Vue.prototype.postSlient =function () {
    options.store.commit('ajaxState',{show:false});
    return options.http.post(...arguments)
  };
  Vue.prototype.get = function () {
    return options.http.get(...arguments)
  };
  Vue.prototype.getSlient = function () {
    options.store.commit('ajaxState',{show:false});
    return options.http.get(...arguments)
  };
  Vue.prototype.jsonlint = function(jsonStr) {
    if(!jsonStr)return '';
    try{
      jsonlint.parse(jsonStr)
    }catch(e){
      return e.toString()
    }
    return ''
  }
}



