import Vue from 'vue';
import Router from 'vue-router';

const home = (r) => require(['../module/home.vue'], r);
const api = (r) => require(['../module/apiList/api.vue'], r);
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'mock',
      meta: {level: 'a1', title: 'home'},
      component: home,
      children:[{
        path: 'api',
        name: 'api',
        meta: {level: 'b1', title: 'api'},
        component: api,
      }
      ]
    },
    {path: '/**', redirect: '/api'},
  ],
});



