// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './module/index.vue'

import baseRouter from 'common/baseRouter';
import baseStore from 'common/baseStore';
import router from './router'
import pageStore from './store';
import {fly} from 'common/network';
import vuePlugin from 'common/vuePlugin/indexV2';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

import './module/base.css';
import 'assets/animate.css'

Vue.config.productionTip = true;
window.log = console.log.bind(console);
let store = baseStore(pageStore);
let http = fly(store);
baseRouter(router, store);


Vue.use(ElementUI);
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(vuePlugin,{store:store,http:http});
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render (h) { return h(App) }
})



