<style scoped>
  .bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 9999;
  }

  .msg-show {
    position: fixed;
    width: 14rem;
    padding: 1.2rem 1rem;
    top: 50%;
    left: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    margin-left: -7rem;
    border-radius: 0.25rem;
    background: #fff;
    font-size: 0.75rem;
    line-height: 1.4;
    color: #333;
    z-index: 9999;
  }

  .msg-show a {
    position: absolute;
    top: 0;
    right: 0;
    padding: 0.25rem;
    color: #cccccc;
  }
</style>
<template>
  <div>
    <transition name="fade" mode="out-in" appear>
      <div v-show="s" class="bg" @click="hide"></div>
    </transition>
    <transition name="fade" mode="out-in" appear>
      <div v-show="s" :class="(s)?'msg-show showing':'msg-show'">
        <div class="show-content">
          <div class="f-tac f-tac" v-html="msgShow"></div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        timer: null,
        s: false,
        msg: '',
        cb:''
      };
    },
    computed: {
      msgShow() {
        return this.msg.replace(/<script.*>/, '').replace(/<\/script>/, '');
      },
    },
    methods: {
      show(msg, time, cb) {
        let self = this;
        this.cb=cb;
        self.msg = msg;
        self.s = true;
        if (self.timer) {
          clearTimeout(self.timer);
          self.timer = null;
        }
        self.timer = setTimeout(() => {
          self.hide();
        }, (time) ? time : 3000);
      },
      hide() {
        const self = this;
        if (self.timer) {
          clearTimeout(self.timer);
          self.timer = null;
        }
        this.s = false;
        if(typeof this.cb === 'function'){
          this.cb();
          this.cb = '';
        }
      },
    },
  };
</script>



